<template>
  <v-card flat>
    <v-snackbar v-model="snackbar" absolute top right color="success">
      {{
        this.$route.params.id
          ? ' .'
          : ' '
      }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

    <v-form ref="form" @submit.prevent="submit">
      <v-container fluid>
        <v-row>
          <v-col cols="12" sm="12">
            <v-text-field
              v-model="form.title"
              :rules="rules.title"
              color="purple darken-2"
              label=""
              required
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <small></small>
            <div class="my-5">
              <div v-if="editor">
                <v-btn
                  @click="setLink"
                  :class="{ 'is-active': editor.isActive('link') }"
                >
                  <v-icon>mdi-link-variant</v-icon>
                </v-btn>
                <v-btn
                  @click="
                    editor
                      .chain()
                      .focus()
                      .unsetLink()
                      .run()
                  "
                  :disabled="!editor.isActive('link')"
                >
                  <v-icon>mdi-link-variant-off</v-icon>
                </v-btn>
                <v-btn
                  @click="
                    editor
                      .chain()
                      .focus()
                      .setHardBreak()
                      .run()
                  "
                >
                  <v-icon>mdi-wrap</v-icon>
                </v-btn>
                <editor-content :editor="editor" />
              </div>
            </div>
            <EditorContent :editor="editor" />
          </v-col>
          <v-col cols="12" sm="12">
            <v-select
              v-model="form.category"
              :items="categories"
              :rules="rules.category"
              color="pink"
              label=""
              required
            ></v-select> </v-col
          ><v-col cols="12" sm="12">
            <v-text-field
              v-if="form.category == ' '"
              v-model="form.youtubeUrl"
              :rules="rules.youtubeUrl"
              color="purple darken-2"
              label="YouTube "
            ></v-text-field>
            <v-text-field
              v-if="form.category == ' '"
              v-model="form.imageUrl"
              :rules="rules.imageUrl"
              color="purple darken-2"
              label=" "
            ></v-text-field>
          </v-col>
        </v-row>
      </v-container>
      <v-card-actions>
        <v-btn
          class="my-5 ml-3"
          v-if="this.$route.params.id"
          depressed
          color="#9575cd"
          dark
          onclick="history.back()"
          >Go Back</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn text @click="resetForm">
          
        </v-btn>
        <v-btn
          :disabled="!formIsValid"
          text
          color="primary"
          type="submit"
          @click="submit"
        >
          {{ this.$route.params.id ? '' : '' }}
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>
<script>
import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';
import router from '../../router/router';

import { Editor, EditorContent } from '@tiptap/vue-2';
import StarterKit from '@tiptap/starter-kit';

import HardBreak from '@tiptap/extension-hard-break';
import Paragraph from '@tiptap/extension-paragraph';

import Link from '@tiptap/extension-link';

export default {
  name: 'PostUpsertForm',
  components: {
    EditorContent,
  },
  data() {
    const defaultForm = Object.freeze({
      title: '',
      youtubeUrl: '',
      imageUrl: '',
      category: '',
    });

    return {
      localPost: '',
      form: Object.assign({}, defaultForm),
      rules: {
        category: [(val) => (val || '').length > 0 || ''],
        title: [(val) => (val || '').length > 0 || ''],
        // content: [(val) => (val || '').length > 0 || ''],
        link: [(val) => (val || '').length > 0 || '  '],
        youtubeUrl: [
          (val) => (val || '').length > 0 || '  ',
        ],
        imageUrl: [(val) => (val || '').length > 0 || '  '],
      },
      categories: [' ', ' '],
      snackbar: false,
      defaultForm,
      editor: null,
    };
  },
  created() {
    this.getPostById();
  },
  mounted() {
    if (!this.$route.params.id) {
      this.editor = new Editor({
        content: `<p># # # #Queer #KoreanDiaspora #Christian<br><br>Presented by KREPAS x KRU Project #<br><br>Preacher: <br>Translator: <br>Editor: <br><br>*   KREPAS Project<br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://www.krepas.com/">https://www.krepas.com/</a><br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://www.facebook.com/krepasproject">https://www.facebook.com/krepasproject</a><br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://www.instagram.com/krepasproject/">https://www.instagram.com/krepasproject/</a><br><br>*  Korean Rainbow United<br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://koreanrainbowunited.org/">https://koreanrainbowunited.org/</a><br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://www.facebook.com/Korean-Rainb">https://www.facebook.com/Korean-Rainb</a>...<br><br>    6()          .<br>If you want to join our interactive Zoom worship service on last Sunday each month [at 6 pm EST], please email us.<br><br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="mailto:krepasproject@gmail.com">krepasproject@gmail.com</a><br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="mailto:koreanrainbowunited@gmail.com">koreanrainbowunited@gmail.com</a><br><br>  .    .<br>If you want to dialogue, please contact us. We are eager to talk.<br><a target="_blank" rel="noopener noreferrer nofollow" class="my-custom-class" href="https://koreanrainbowunited.org/#/counsel">https://koreanrainbowunited.org/#/counsel</a></p>`,
        extensions: [
          StarterKit,
          Link.configure({
            openOnClick: true,
            autolink: true,
            linkOnPaste: true,
            HTMLAttributes: {
              class: 'my-custom-class',
            },
          }),
          HardBreak.configure({
            keepMarks: true,
          }),
          Paragraph,
        ],
      });
    }
  },

  computed: {
    formIsValid() {
      return (
        this.form.title &&
        ((this.form.category == ' ' && this.form.imageUrl) ||
          (this.form.category == ' ' && this.form.youtubeUrl))
      );
    },
  },

  methods: {
    ...mapActions(['newPost']),

    resetForm() {
      this.form = Object.assign({}, this.defaultForm);
      this.$refs.form.reset();
      this.editor.commands.clearContent();
    },
    getPostById() {
      if (this.$route.params.id) {
        axios
          .get(`http://localhost:5000/api/posts/${this.$route.params.id}`)
          .then((res) => {
            const localPost = res.data;
            this.form.title = localPost.title;
            this.content = localPost.content;
            this.form.youtubeUrl = localPost.youtubeUrl
              ? `https://youtu.be/${localPost.youtubeUrl}`
              : '';
            this.form.imageUrl = localPost.imageUrl
              ? `https://drive.google.com/file/d/${localPost.imageUrl}/view?usp=sharing`
              : '';
            this.form.category = localPost.category;
            this.editor = new Editor({
              content: this.content,
              extensions: [
                StarterKit,
                Link.configure({
                  openOnClick: true,
                  autolink: true,
                  linkOnPaste: true,
                  HTMLAttributes: {
                    class: 'my-custom-class',
                  },
                }),
                HardBreak.configure({
                  keepMarks: true,
                }),
                Paragraph,
              ],
            });
          });
      }
    },
    async submit() {
      // console.log(this.editor.getHTML());
      if (this.$route.params.id) {
        await axios
          .put(
            `http://localhost:5000/api/posts/${this.$route.params.id}`,
            {
              title: this.form.title,
              content: this.editor.getHTML(),
              youtubeUrl: this.form.youtubeUrl.split('youtu.be/')[1],
              imageUrl: this.form.imageUrl.split('/')[5],
              category: this.form.category,
            },
            {
              headers: {
                Authorization: `Bearer ${window.localStorage.getItem('token')}`,
              },
            }
          )
          .then((res) => {
            return res.data;
          })
          .then((this.snackbar = true))
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.newPost({
          title: this.form.title,
          content: this.editor.getHTML(),
          youtubeUrl: this.form.youtubeUrl.split('youtu.be/')[1],
          imageUrl: this.form.imageUrl.split('/')[5],
          category: this.form.category,
        });
        this.snackbar = true;
        this.resetForm();
      }
    },
    setLink() {
      const previousUrl = this.editor.getAttributes('link').href;
      const url = window.prompt('URL', previousUrl);

      // cancelled
      if (url === null) {
        return;
      }

      // empty
      if (url === '') {
        this.editor
          .chain()
          .focus()
          .extendMarkRange('link')
          .unsetLink()
          .run();

        return;
      }

      // update link
      this.editor
        .chain()
        .focus()
        .extendMarkRange('link')
        .setLink({ href: url })
        .run();
    },
  },
};
</script>
<style lang="scss">
.ProseMirror {
  min-height: 300px;
  border: 2px solid;
  border-radius: 5px;
}
.ProseMirror {
  a {
    color: #68cef8;
  }

  code {
    font-size: 0.9rem;
    padding: 0.25em;
    border-radius: 0.25em;
    background-color: rgba(#616161, 0.1);
    color: #616161;
    box-decoration-break: clone;
  }
}
</style>
